//definir uma fonte
//este  o Global styles - Global.modules.scss
body {
  margin: 0;
  font-family: "Montserrat", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
  font-variation-settings:
  "wdth" 100;
}
h1 {
    font-size: 24px;
    color: #2d2c2c;
    margin-bottom: 24px;
    text-align: center;
  }
  .Input {
    background-color: #F0f0f0;
    margin: 20px;
    margin-bottom: 1px;
    box-sizing: border-box;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    border-radius: 10px;
    border: 2px solid transparent; // Adiciona uma borda transparente por padro
    width: 100%;
    padding: 12px;
}

.InputErro {
    border-color: red; // Adiciona uma borda vermelha quando h erro
}

.InputSucesso {
    border-color: green; // Adiciona uma borda verde quando no h erro
}
.MensagemErro {
  color: red; // Cor do texto vermelho para indicar erro
  font-size: 12px; // Tamanho da fonte da mensagem de erro
  margin-top: 2px;// Espaamento superior para separar a mensagem do campo de entrada
}

.Rotulo{
  display: block;
  font-weight: 600;
  font-size: 16px;
  line-height: 19%;
  margin-top: 20px;
}

