.itemCard {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%; 
  max-width: 900px;
  max-height: 210px; 
  margin: 20px auto;
  border-radius: 20px;
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.70);
  padding: 20px;
  color: #000;
  overflow: hidden;
  transition: all 0.3s ease;
  margin-bottom: 50px;
  animation: fadeIn 2s ease-in-out;
  transition: transform 0.2s ease-in-out;
  
.itemCard:hover{
  transform: scale(1.1);
}

  @keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
  }

  @media (max-width: 768px) {
    /* Styles for tablets */
    flex-direction: column; /* Stack image and content */
    max-width: 100%; /* Full width on tablets */
  }

  @media (max-width: 576px) {
    /* Styles for mobile */
    max-width: 100%; /* Full width on mobile */
    padding: 10px; /* Reduce padding for smaller screens */
  }
  .cardContent {
    flex: 1;
    flex-direction: column; 
  }

  .cardHeader {
    margin-top: 55px;
    display: flex;
    justify-content: flex-start; 
    flex-direction: column;
  }

  .cardTitle {
    font-size: 1.5rem;
    color:#fff;
    margin: 0;
    flex: 1;
  }
  .cardDescription {
    font-size: 1rem;
    color:#fff;
    padding: 10px;
  }
  .cardImage {
    height: 100%;
    object-fit: cover; 
  }

  .btn {
    display: flex;
    justify-content: left;
    align-items: center;
    padding: 10px 20px;
    border-radius: 10px;
    text-decoration: none;
    font-weight: bold;
    text-transform: uppercase;
  }

  &.color1 { background: #FF6766; }
  &.color2 { background: #F9C20B; }
  &.color3 { background: #ABCD52; }
  &.color4 { background: #1AB8FF; }
}


.btn button {
  text-decoration: none;
}

.btn a {
  text-decoration: none;
}

.btn {
  display: flex;
  justify-content: center; 
  align-items: center; 
  text-decoration: none;
}

.Modal {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
  width: 300px;
  height: 200px;
  z-index: 1000;
}

.cardCloseButton {
  position: absolute;
  top: 10px;
  right: 10px;
  background: none;
  border: none;
  font-size: 18px;
  cursor: pointer;
}

.Overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1000;
}
